@import url('https://fonts.googleapis.com/css2?family=Silkscreen&display=swap');

.loading-wrapper {
    width: 400px;
    height: 120px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: #1d1d1f;
}

.loading-circle {
    width: 40px;
    height: 40px;
    position: absolute;
    border-radius: 50%;
    background-color: black;
    left: 15%;
    transform-origin: 50%;
    animation: circle .3s alternate infinite ease;
}

@keyframes circle {
    0% {
        top: 60px;
        height: 10px;
        border-radius: 100px 100px 50px 50px;
        transform: scaleX(1.7);
    }
    40% {
        height: 40px;
        border-radius: 50%;
        transform: scaleX(1);
    }
    100% {
        top: 0%;
    }
}

.loading-circle:nth-child(2) {
    left: 45%;
    animation-delay: .2s;
}

.loading-circle:nth-child(3) {
    left: auto;
    right: 15%;
    animation-delay: .3s;
}

.loading-shadow{
    width:40px;
    height:8px;
    border-radius: 50%;
    background-color: rgba(0,0,0,.5);
    position: absolute;
    top:62px;
    transform-origin: 50%;
    z-index: -1;
    left:15%;
    filter: blur(1px);
    animation: shadow .3s alternate infinite ease;
}

@keyframes shadow{
    0%{
        transform: scaleX(1.5);
    }
    40%{
        transform: scaleX(1);
        opacity: .7;
    }
    100%{
        transform: scaleX(.2);
        opacity: .4;
    }
}
.loading-shadow:nth-child(4){
    left: 45%;
    animation-delay: .2s
}
.loading-shadow:nth-child(5){
    left:auto;
    right:15%;
    animation-delay: .3s;
}

.loading-wrapper span{
    margin-top: 20px;
    position: absolute;
    top:60px;
    font-family: 'Silkscreen', cursive;
    font-size: 45px;
    letter-spacing: 12px;
    color: white;
    left:15%;
}